<template>
  <div class="wrapper">
    <div class="content">
      <div class="c-left">
        <div class="c-top">
          <ctitle :title="'专项督办信访总量'"></ctitle>
          <div class="inner">
            <div class="detail">
              <allCount :count="zxdbCount"></allCount>
            </div>
            <div class="chart">
              <pie-chart3D :params="zxdbPieParams"></pie-chart3D>
            </div>
          </div>
        </div>
        <div class="c-btm">
          <ctitle :title="'年度专项督办信访总量趋势'"></ctitle>
          <LinesR :params="qsLineParams"></LinesR>
        </div>
      </div>
      <div class="c-right">
        <div class="r-top">
          <ctitle :title="'国家专项督办信访总量'"></ctitle>
          <div class="inner">
            <allCount class="all-count" :count="gjzxdbCount"></allCount>
            <pie-chart3D class="chart" :params="gjzxdbPieParams"></pie-chart3D>
          </div>
        </div>
        <div class="r-bottom">
          <ctitle :title="'省内专项督办信访总量'"></ctitle>
          <div class="inner">
            <div class="chart" v-for="(item, index) of snzxdbArr" :key="index">
              <loopPie :params="item.params"></loopPie>
              <p class="label">{{ item.name }}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {get_http} from "@/api/index";
  import ctitle from "@/components/title/index";
  import PieChart3D from "@/components/pie/PieChart3D";
  import LinesR from "@/components/line/LinesR";
  import allCount from "@/components/list/allCount";
  import loopPie from "@/components/xzfx/LoopPie";

  export default {
    name: "xzfx",
    components: {
      ctitle,
      PieChart3D,
      LinesR,
      allCount,
      loopPie,
    },
    data() {
      return {
        // 1.专项督办信访总量-总数
        zxdbCount: -1,
        // 2.专项督办信访总量-饼图
        zxdbPieParams: {
          data: [],
          color: ["#655CDD", "#50B3FF","#62EDAC",'#ffc833', '#d93e75','#eb9002'],
          // color:this.$STANDARD.pieColors,
          angle: 20,
          radius: "30%",
          labelRadius: 30,
          styleObj: {
            width: "100%",
            height: "100%"
          },
          isBr: true,
          labelPer: true
        },
        // 3.年度专项督办信访总量趋势
        qsLineParams: {
          data: [],
          colors: [
            {
              line: "rgba(19, 240, 255, 1)",
              start: "rgba(19, 240, 255, .5)",
              end: "rgba(19, 240, 255, .5)"
            }
          ],
          option: {
            axisLabelColor: "#fff",
            yaxisLabelColor: "#82d8ff",
            legendfontSize: 18,
            splitLineShow: true,
            legendShow: false,
            legenditemWidth: 22,
            legenditemHeight: 4,
            axisLabelShow: true,
            axisTickShow: false,
            axisCategoryName: "年",
            // axisYName: "件次",
            axisLabelSize: 14,
            scales: true,
            axisLabelFontSize: 16,
            yaxisLabelFontSize: 14,
            Tofixed: false,
            smooth: true,
            areaShow: 0,
            smoothMonotone: "none",
            dataZoomshow: false,
            legendIcon: "rect",
            left: "10%",
            right: "10%",
            top: "15%",
            bottom: "10%",
            legendTop: "0%",
            legendRight: "10%"
          },
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          map: {
            "value": "总量"
          },
          xName: "name"
        },
        // 4.国家专项督办信访总量-总数
        gjzxdbCount: -1,
        // 5.国家专项督办信访总量-饼图
        gjzxdbPieParams: {
          data: [],
          color: ["#655CDD", "#50B3FF","#62EDAC",'#ffc833', '#d93e75','#eb9002'],
          // color:this.$STANDARD.pieColors,
          angle: 20,
          radius: "40%",
          labelRadius: 30,
          styleObj: {
            width: "100%",
            height: "100%"
          },
          isBr: true,
          labelPer: true
        },
        // 6.省内专项督办信访总量
        snzxdbArr: [
          {name: "省内专项督办-省级", params: {data: [], options: {}}},
          {name: "省内专项督办-市级", params: {data: [], options: {}}},
          {name: "省内专项督办-县级", params: {data: [], options: {}}}
        ],
        snzxdbPieParams: {
          options: {
            colors: this.$STANDARD.gradientColors,
            radius: ["58%", "78%"],
            center: ["50%", "50%"],
            centerRadius: ["45%", "46%"],
            labelRich: {
              b: this.fontSize(0.26),
              d: this.fontSize(0.28),
              bHeight: this.fontSize(0.4)
            },
            length: 20,
            length2: 70,
            itemLabelShow: true, //数据扇区label是否显示
            //图例
            legend: {
              show: false
            },
            rollFlag: true
          }
        }
      };
    },
    mounted() {
      this.getAPI(this.year)
    },
    methods: {
      getAPI(year) {
        // 1.专项督办信访总量-总数
        get_http("mdfxyjypxzfx", {
          gd_id2:'专项督办信访总量',
          year: year
        })
          .then(res => {
            this.zxdbCount = res.data.data[0].value;
          })
          .catch(err => {
            console.log(err);
          });
        // 2.专项督办信访总量-饼图
        get_http("mdfxyjypxzfx", {
          gd_id2:'专项督办信访饼图',
          year: year
        })
          .then(res => {
            this.zxdbPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 3.专项督办信访趋势
        get_http("mdfxyjypxzfx", {
          gd_id2:'专项督办信访趋势',
          year: '[now-5, now]'
        })
          .then(res => {
            this.qsLineParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 4.国家专项督办信访总量-总数
        get_http("mdfxyjypxzfx", {
          gd_id2:'国家专项督办信访总量',
          year: year
        })
          .then(res => {
            this.gjzxdbCount = res.data.data[0].value;
          })
          .catch(err => {
            console.log(err);
          });
        // 5.国家专项督办信访总量-饼图
        get_http("mdfxyjypxzfx", {
          gd_id2:'国家专项督办信访饼图',
          year: year
        })
          .then(res => {
            this.gjzxdbPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        this.getSnzxdbPieAPI(year);
      },
      // 6.省内专项督办信访总量
      getSnzxdbPieAPI(year) {
        for (let item of this.snzxdbArr.values()) {
          item.params.options = this.snzxdbPieParams.options;
          get_http("mdfxyjypxzfx", {
            gd_id2:item.name,
            year: year,
          })
            .then(res => {
              item.params.data = res.data.data;
            })
            .catch(err => {
              console.log(err);
            });
        }
      },
      fontSize(res) {
        let docEl = document.documentElement,
          clientWidth =
            window.innerWidth ||
            document.documentElement.clientWidth ||
            document.body.clientWidth;
        if (!clientWidth) return;
        let fontSize = 100 * (clientWidth / 1920);
        return res * fontSize;
      }
    },
    computed: {
      year() {
        return this.$store.state.year;
      }
    },
    watch: {
      year(val) {
        this.getAPI(val)
      }
    }
  };
</script>

<style lang="stylus" scoped>
  .wrapper
    position relative
    top 0.8rem
    height calc(100% - 0.8rem)

    .content
      width 100%
      height 100%
      display flex
      padding .2rem .3rem
      box-sizing border-box

      .c-left
        width 12rem
        flex 1
        height 100%
        position relative
        display flex
        flex-direction column
        border-right 1px dashed #91e3f7
        .c-top
          flex 1
          display flex
          flex-direction column
          .inner
            display flex
            width 100%
            height 100%
            .detail
              flex 1
              width 100%
              height 100%
              justify-content center
              align-items center
              display flex
            .chart
              flex 1.5
              width 100%
              height 100%
        .c-btm
          flex 1
          display flex
          flex-direction column
          width 100%
          height 100%
      .c-right
        flex 1.5
        height 100%
        position relative
        display flex
        flex-direction column

        .r-top
          height 50%
          display flex
          flex-direction column
          .inner
            flex 1
            display flex
            justify-content center
            align-items center
            width 100%
            height 100%
            .all-count
              flex 1
            .chart
              flex 1.5
        .r-bottom
          height 50%
          .inner
            display flex
            width 100%
            height 100%
            .chart
              width 100%
              height 75%
            .label
              font-size 0.2rem
              text-align center
              color #fefefe
              font-weight 700
</style>
