<!--
  @auth:lcw
  @date:2020-4-9
  总数（件次）
-->
<template>
  <div class="all-count" v-loading="count<0" :element-loading-background="$STANDARD.loadingBgColor">
    <div class="inner" v-if="count>=0">
      <incNumber class="number" :incNumber="count" :style="{width:numMaxLength*32+'px'}"></incNumber>
      <span class="unit">件 次</span>
    </div>
  </div>
</template>
<script>
  import incNumber from "@/components/incNumber/index";

  export default {
    name: "allCount",
    components: {
      incNumber
    },
    props: {
      count: {
        type: Number,
        default: -1
      }
    },
    data() {
      return {
        numMaxLength: this.count.toString().length
      }
    },
    watch: {
      count() {
        if(this.count){
          this.numMaxLength = this.count.toString().length;
        }
      }
    }
  }
</script>

<style lang="stylus" scoped>
  .all-count
    position relative
    width 100%
    height .5rem
    text-align center
    display flex
    justify-content center
    align-items center
    .inner
      font-size 0
      .number
        padding 0 10px
        font-size 48px
        color #ffde00
        text-shadow 0 0 20px rgba(255, 200, 51, 0.3)
      .unit
        color #5aceff
        font-size 24px
</style>
